.SideDrawer {
  position: fixed;
  width: 200px;
  max-width: 70%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 200;
  background-color: var(--white-color);
  padding: 16px 0px;
  box-sizing: border-box;
  transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
  .SideDrawer {
     display: none;
  }
}

.Open {
  transform:  translateX(0);
}

.Close {
  transform: translateX(-100%);
}

.Logo {
  height: 15%;
  margin-bottom: 16px;
}

